<template>
	<div>
		<p class="title">考试说明:</p>
		<div style="padding:0 0.3rem  0.2rem  0.3rem;font-size: 0.3rem;border-bottom: 0.02rem solid #cccccc;">
			请合理安排答卷时间,一旦开始答卷,则会进行倒计时；考试中途允许离开,系统会保留你的答题进度,但倒计时不会停止;<br />
			时间结束,不管是否在线,系统自动交卷。
		</div>
		<!-- @click="show showError"-->
		<div class="item" v-for="item in list" @click="show(item.type,item.id)">
			<div class="top">
				<div class="icon"></div>
				<span style="margin-left: 0.2rem;">{{item.className}}</span>
				<span v-if="item.type==1" style="float: right;color: #fa8a12;margin-right: 0.3rem;">未进行</span>
				<span  v-if="item.type==2" style="float: right;color: #fa8a12;margin-right: 0.3rem;">进行中</span>
				<span v-if="item.type==3" style="float: right;color: #fa8a12;margin-right: 0.3rem;">已结束</span>
			</div><div class="bottom">
				<p style="margin-top: 0.3rem;">{{item.name}}</p>
				<p style="font-size: 0.26rem;color: #666666;margin-top: 0.2rem;">
					考试时长{{item.duration}}分钟; {{item.count}}道题目; 总分:{{item.score}}分
					
				</p>
				<p style="font-size: 0.26rem;color: #999999;margin-top: 0.1rem;">
					<p style="font-size: 0.26rem;color: #999999;">开卷时间: {{item.startTime}}</p>
					<p style="font-size: 0.26rem;color: #999999;">收卷时间: {{item.endTime}}</p>
				</p>
				<p style="color: #f21b1b;font-size: 0.26rem;margin-top: 0.1rem;">成绩: {{item.grade}}</p>
			</div>
		</div>
		
		<!--<div class="item" @click="showError">
			<div class="top">
				<div class="icon"></div>
				<span style="margin-left: 0.2rem;">A班</span>
				<span style="float: right;color: #fa8a12;margin-right: 0.3rem;">进行中</span>
			</div>
			<div class="bottom">
				<p style="margin-top: 0.3rem;">事件名称,20字内</p>
				<p style="font-size: 0.26rem;color: #666666;margin-top: 0.2rem;">
					考试时长30分钟; 50道题目; 总分100分
					
				</p>
				<p style="font-size: 0.26rem;color: #999999;margin-top: 0.1rem;">
					<span>开卷时间: 2018/01/12</span>
					<span style="margin-left: 0.6rem;">收卷时间: 2018/01/14</span>
				</p>
				<p style="color: #f21b1b;font-size: 0.26rem;margin-top: 0.1rem;">成绩: --</p>
			</div>
			
		</div>-->
		<div style="height: 1rem;"></div>
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				  list:[],
				  action:this.$route.params.action
			}
		},
		mounted(){
			
			this.getData()
		},
		methods:{
			show(type,id){
				if(type==1||type==2){
						
				this.$popup({
					  title: '提示',
          content: '即将开始考试,并进行倒计时,考试时长30分钟,现在就进行考试吗?'
				}).then((res)=>{
					
					if(res=='sure'){
						this.$router.push({name: 'Test' ,query:{Action:3,testId:id}})
					}
				})
				}else{
						this.$popup({
					  title: '提示',
          content: '本次考试时间还没到,请耐心等待',
          showCancel:false
				})
				}
				
				
			
			},
			showError(){
			
				
			},
			getData(){
				var obj={
					"s":this.$store.state.sessionId,
					"a":this.action
				}
				this.$Api.ExamList(obj).then((res)=>{
					
					
					this.list=res.q.examPaper

					
				})
				
			}
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.title{
	color: #fa8a12;
	font-size: 0.3rem;
	padding: 0.3rem;
}
.item{
	width: 6.9rem;
	
	border: 0.02rem solid #CCCCCC;
	border-radius:0.2rem ;
	margin:0.3rem auto;
	.top{
		width: 100%;
		background: #f8f8f8;
		height: 0.8rem;
		border-radius:0.2rem 0.2rem 0 0 ;
		font-size:0.3rem;
		line-height:0.8rem;
		.icon{
			width: 0.36rem;
			height: 0.4rem;
			background: url(../../assets/text.png) no-repeat;
			background-size:100% 100% ;
			margin-left: 0.3rem;
			margin-top: 0.2rem;
			float: left;
		}
		span{
			float: left;
		}
	}
	.bottom{
		font-size: 0.3rem;
		padding-left: 0.3rem;
		box-sizing: border-box;
	}
	
}


</style>


